import axios from 'axios';
import { get as lodashGet } from 'lodash';
const API_URL = process.env.REACT_APP_API || 'http://localhost:3030';

const headers = () => {
  const headers = {};

  if (localStorage.getItem('TOKEN')) headers['Authorization'] = localStorage.getItem('TOKEN');

  return headers;

}

export const post = (endpoint, data) => {
  console.log(`POST /${endpoint}`);
  return axios.post(`${API_URL}/${endpoint}`, data, { headers: headers() })
    .then(response => {
      if (lodashGet(response, 'data.status') === 'OK') {
        return lodashGet(response, 'data.data');
      }
      return null;
    }).catch(error => {
      const err = lodashGet(error, 'response.data.error', {message: 'Unknown Error !!'});
      throw err;
    });
};

export const get = (endpoint) => {
  console.log(`GET /${endpoint}`);
  return axios.get(`${API_URL}/${endpoint}`, { headers: headers() })
    .then(response => {
      if (lodashGet(response, 'data.status') === 'OK') {
        return lodashGet(response, 'data.data');
      }
      return null;
    }).catch(error => {
      const err = lodashGet(error, 'response.data.error', {message: 'Unknown Error !!'});
      throw err;
    });
};

export default {
  get,
  post
};
