import React, { useState } from 'react';

import API from '../../utils/api';
import { AppHistory } from '../../App';

export default (props, context) => {
  const [state, setState] = useState({
    error: '',
    firstName: '',
    lastName: '',
    email: '',
    password: ''
  });
  const inputChanged = ({ target }) => {
    setState({
      ...state,
      [target.name]: target.value,
      error: ''
    });
  }

  const register = async (event) => {
    event.preventDefault();
    const { firstName, lastName, email, password } = state;
    try {
      const data = await API.post('auth/register', {
        firstName,
        lastName,
        email,
        password
      });
      AppHistory.push('/login', {username: data.username});
    } catch (error) {
      setState({
        ...state,
        error: error.message
      });
    }
  }
  return (
    <form onSubmit={register}>
      <div className="login-container">
        <div className="text-center">
          <h2 className="heading">Signup</h2>
        </div>
        <div className={"text-center error-text " + (!state.error ? 'visibility-hidden': '')}>
          <span>{state.error}</span>
        </div>
        <input type="text" className="form-control col-sm-9" placeholder="First Name" name="firstName" id="firstName" value={state.firstName} onChange={inputChanged} required />
        <input type="text" className="form-control col-sm-9" placeholder="Last Name" name="lastName" id="lastName" value={state.lastName} onChange={inputChanged} required />
        <input type="text" className="form-control col-sm-9" placeholder="Email" name="email" id="email" value={state.email} onChange={inputChanged} required />
        <input type="password" className="form-control col-sm-9" placeholder="Password" name="password" id="password" value={state.password} onChange={inputChanged} required />

        <div className="text-center">
          <button type="submit" className="btn login-btn">Sign up</button>
        </div>
      </div>
    </form>
  );
}
