import React, { useState, useCallback } from "react";
import debounce from 'lodash/debounce';
import "./translate.scss";
const API = "https://rpi4.regdelivery.de/translate/api/";

const useDebounce = (callback, delay) => {
  const debouncedFn = useCallback(
    debounce((...args) => callback(...args), delay),
    [delay]
  );
  return debouncedFn;
};

export default () => {
  const [loading, setLoading] = useState(false);
  const [originalText, setOriginalText] = useState('');
  const [translatedText, setTranslatedText] = useState('');
  const debouncedSave = useDebounce(async (nextValue) => {
    setLoading(true);
    try {
      const result = await (await fetch(`${API}?text=${nextValue}`)).text();
      if(result) {
        setTranslatedText(result);
      }
    } catch (error) {
      setTranslatedText('Error Translating');
    }
    setLoading(false);
  }, 1000);

  const handleChange = (event) => {
    const { value: nextValue } = event.target;
    setOriginalText(nextValue);
    debouncedSave(nextValue);
  };

  return (
    <div className="translate-container">
      <div className="header">Translator</div>
      <div className="inputs">
        <div className="origin">
          <textarea rows="5" value={originalText} onChange={handleChange} />
        </div>
        <div className="spinner">
          {loading && <i className="fas fa-cog fa-spin"></i>}
        </div>
        <div className="destination">
          <textarea rows="5" value={translatedText} onChange={() => {}} />
        </div>
      </div>
    </div>
  );
};
